import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';

class ColoredLabel extends React.Component {
  render() {
    return <p style={{color: "green"}} >{this.props.msg}</p>
  }
}

class RedH1 extends React.Component {
  render() {
    return <h1 style={{color: "red"}} >{this.props.children}</h1>
  }
}


class TopLevel extends React.Component {
  constructor(props) {
    super(props);
    this.state = { msg: "initial state bitches!" };
  }
  handleClick() {
    var input = prompt("get new state");
    this.setState({ msg: input });
  }
  render() {
    return (<>
    <p class="myelement" onClick={() => { this.handleClick() }} >{this.state.msg}</p>
    <ColoredLabel msg="message from top level to coloredlabel"/>
    <RedH1>redh1 text</RedH1>
    </>
    )
  }
}


ReactDOM.render(<TopLevel/>, document.getElementById("root"));